@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,600,700&display=swap);
.banner {
    width: 100%;
    height: 40vh;
    max-height: 40vh;
    overflow: hidden;
}
.banner-img {
    width: 100%;
}
@media only screen and (min-width: 1100px) {
    .banner-img {
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}
@media only screen and (max-width: 1100px) {
    .banner {
        width: 100%;
        height: auto;
        overflow: hidden;
    }
    .banner-img {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
}
@media only screen and (max-width: 700px) {
    .banner {
        width: 100%;
        height: auto;
        overflow: hidden;
    }
    .banner-img {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

.recipe-container {
    margin: 0 auto;
    width: 100%;
}
.recipes-heading {
    text-align: center;
}
.recipes-heading h2 {
    font-size: 3rem;
    padding: 1rem 0.5rem;
    color: #ffffff;
    background: #ffffff;
    text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8;
}
.recipe {
    width: 80%;
    margin: 0 auto;
    padding: 1rem;
}
.recipe-img {
    width: 100%;
}
.recipe-name {
    color: #ffffff;
    text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8;
}
.recipe-name-container {
    background-image: url(/honey/static/media/background.23716469.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    text-align: center;
}
.recipe-link:hover {
    text-decoration: none;
}

/* Extended Recipe */
.extended-recipe-container {
    width: 100%;
}
.extended-recipe {
    box-shadow: 11px 28px 58px 9px rgba(0, 0, 0, 0.48);
    background: #f7c1c1;
    width: 95%;
    margin: 1rem auto;
    padding: 0.5rem;
}
.extended-recipe-heading {
    color: #ffffff;
    text-shadow: 4px 3px 0px #7a7a7a;
    text-align: left;
}
.image-container {
    width: 100%;
}
.extended-recipe-img {
    width: 50%;
}
.titles {
    font-size: 1.3rem;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 0.2rem;
    -webkit-margin-after: 0.2rem;
            margin-block-end: 0.2rem;
}
.text {
    -webkit-margin-before: 0.5rem;
            margin-block-start: 0.5rem;
    -webkit-margin-after: 0.5rem;
            margin-block-end: 0.5rem;
}

.line {
    width: 10rem;
    height: 5px;
    background: grey;
    margin: 2rem auto;
}
.background {
    min-height: calc(100vh - 66px);
    background: url(/honey/static/media/404.5e771a94.jpg) center/cover no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
}
.error-container {
    width: 100%;
    margin: 0 auto;
}
.error {
    width: 70%;
    margin: 0 auto;
    text-align: center;
    font-size: 2.5rem;
}
.error h1 {
    font-size: 4rem;
}
.error h1 {
    color: #ffffff;
    text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8;
    text-transform: uppercase;
}
.error p {
    color: #ffffff;
    text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8;
    text-transform: uppercase;
}
.error a {
    text-decoration: none;
}

.myButton {
    box-shadow: inset 0px 1px 0px 0px #ffffff;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ffffff), to(#f6f6f6));
    background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
    background-color: #ffffff;
    border-radius: 6px;
    border: 1px solid #dcdcdc;
    display: inline-block;
    cursor: pointer;
    color: #666666;
    font-family: Arial;
    font-size: 3rem;
    font-weight: bold;
    padding: 6px 24px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #ffffff;
}
.myButton:hover {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #f6f6f6), to(#ffffff));
    background: linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
    background-color: #f6f6f6;
}
.myButton:active {
    position: relative;
    top: 1px;
}
@media only screen and (max-width: 700px) {
    .myButton {
        font-size: 2rem;
    }
    .error {
        width: 70%;
        margin: 0 auto;
        text-align: center;
        font-size: 1.5rem;
    }
    .error h1 {
        font-size: 3rem;
    }
}

